@tailwind base;
@tailwind components;
@tailwind utilities;
/* // _general.scss */
/*********************************/
/*             General            */
/*===============================*/
body {
  @apply font-poppins text-base lg:text-lg xl:text-xl bg-darkGray;
}

p {
  @apply text-primaryText leading-8;
}

section {
  overflow: hidden;
}

p {
  @apply leading-relaxed;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  @apply leading-normal text-white;
}

::selection {
  @apply bg-primaryOrange/90 text-white;
}

section {
  @apply py-10 xl:py-16;
}

.section-title {
  @apply text-3xl leading-[2.62rem] lg:text-4xl   xl:text-[2.5rem]  font-medium mb-4 xl:mb-[1.3rem];
}

.hide-scroll {
  @apply overflow-hidden pr-[1.1rem];
}

.swiper-scrollbar.swiper-scrollbar-horizontal {
  height: 8px;
  left: 100%;
  transform: translate(-109%, 40px);
}

.swiper-scrollbar-drag {
  @apply bg-primaryOrange;
}

.section-wrapper {
  @apply bg-darkGray  relative before:-z-1 after:-z-1 bg-transparent w-full before:absolute before:inset-0 before:bg-[url("/assets/images/backgrounds/section_bg.png")]  before:bg-no-repeat before:bg-left-top before:blur-xl  after:absolute after:inset-0 after:bg-[url("/assets/images/backgrounds/section_bg.png")]  after:bg-no-repeat after:bg-right-bottom after:blur-xl after:opacity-70 before:opacity-70;
}

/*********************************/
/*             Helper            */
/*===============================*/
.container {
  @apply relative;
}

/* Cookies */
.cookie-popup-not-accepted {
  @apply block;
  animation: cookie-popup-in 0.5s ease forwards;
}

.cookie-popup-accepted {
  @apply hidden;
}

@keyframes cookie-popup-in {
  from {
    bottom: -6.25rem;
  }
  to {
    bottom: 1.25rem;
  }
}
/* Shapes */
.shape > svg {
  transform: scale(2);
  transform-origin: theme("transformOrigin.top") theme("transformOrigin.center");
}
.shape.marketing-hero {
  clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}

/* Preloader */
#preloader {
  background-image: linear-gradient(45deg, #ffffff, #ffffff);
  z-index: 99999;
  @apply fixed inset-0;
}
#preloader #status {
  @apply absolute left-0 right-0 top-1/2 -translate-y-1/2;
}
#preloader #status .spinner {
  @apply w-10 h-10 relative my-[100px] mx-auto;
}
#preloader #status .spinner .double-bounce1, #preloader #status .spinner .double-bounce2 {
  @apply w-full h-full rounded-full bg-indigo-600/60 absolute top-0 left-0;
  animation: sk-bounce 2s infinite ease-in-out;
}
#preloader #status .spinner .double-bounce2 {
  animation-delay: -1s;
}

@keyframes sk-bounce {
  0%, 100% {
    transform: scale(0);
  }
  50% {
    transform: scale(1);
  }
}
/* Switcher */
.label .ball {
  transition: transform 0.2s linear;
  @apply translate-x-0;
}

.checkbox:checked + .label .ball {
  @apply translate-x-6;
}

.mover {
  animation: mover 1.5s infinite alternate;
}

@keyframes mover {
  0% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(10px);
  }
}
/********************/
/*     Buttons      */
/*==================*/
/* Note: If you want to add your own colors, you can do so by going to this tailwind.config.js file  */
.btn {
  @apply px-10 py-3 text-sm lg:text-base uppercase rounded-[4px] transition-all duration-500 hover:scale-105 bg-primaryOrange text-white cursor-pointer text-center inline-block whitespace-nowrap;
}

/*********************************/
/*         Navbar                  */
/*===============================*/
.navbar {
  @apply absolute top-0 left-0 right-0 z-50 text-white py-7 xl:py-11 font-jost;
}
.navbar .nav-center {
  @apply flex items-center justify-between;
}
.navbar .nav-center .logo {
  @apply w-[7rem] lg:w-[8rem]  xl:w-auto;
}
.navbar .nav-center .menus {
  @apply flex items-center space-x-4;
}
.navbar .nav-center .menus .menu_btn {
  @apply lg:text-[1.5rem] font-jost flex items-center space-x-4 transition-all duration-300;
}
.navbar .nav-center .menus .menu_btn .menu_btn_line {
  @apply w-5 xl:w-8 h-[1px] bg-white inline-block ml-4 transition-all duration-300;
}

.nav-menu {
  @apply fixed top-0 left-0  bg-darkGray transition-all duration-300 w-full  h-0 z-10 overflow-hidden;
}

.nav-menu.active {
  @apply h-full;
}

.my-menu {
  margin-top: 100px;
  box-sizing: border-box;
  width: 100%;
}
.my-menu ul.top-level {
  @apply list-none p-0 m-0 flex justify-between w-full bg-transparent;
}
.my-menu ul.top-level ul {
  @apply py-4 min-w-[280px];
}
.my-menu ul.top-level > li {
  @apply text-[2rem];
}
.my-menu ul.second-level {
  @apply relative mt-8  list-disc;
}
.my-menu ul.second-level > li {
  @apply rounded-l-md cursor-pointer text-xl;
}
.my-menu ul.second-level > li:hover {
  @apply bg-[#3B3B3B] transition-all duration-300;
}
.my-menu ul.second-level li {
  @apply px-4 py-2 whitespace-nowrap;
}
.my-menu ul.second-level li > ul {
  @apply bg-[#3B3B3B]  absolute top-0 left-full  opacity-0 h-full  transition-all duration-300 min-w-0 w-0 -z-50 overflow-visible rounded-md;
}
.my-menu ul.second-level li.active-menu {
  @apply bg-[#3B3B3B];
}
.my-menu ul.second-level li.active-menu > ul {
  @apply opacity-100  min-w-[200px] z-1;
}

.show-menu-list {
  @apply opacity-100  min-w-[200px] z-1;
}

/* // _footer.scss */
/*********************************/
/*         Footer                */
/*===============================*/
.footer .foot-subscribe input::placeholder {
  @apply text-gray-200;
}

.social-icon:hover > svg path {
  fill-opacity: 1;
  transition: 0.3s;
}

.hero {
  @apply relative flex items-center justify-center h-screen max-h-screen  overflow-hidden;
}
.hero .hero_overlay {
  @apply absolute z-2 inset-0 bg-gradient-to-b from-[rgba(0,0,0,0.8)] to-[rgba(0,0,0,0.8)];
}
.hero .hero_video {
  @apply absolute z-1 w-auto min-w-full min-h-full max-w-none 2xl:-mt-[6.4rem];
}
.hero .hero_btn {
  @apply -rotate-90 absolute bottom-[7rem] right-[-5rem] md:right-[-4rem] lg:right-[-3rem] xl:right-[-2rem] font-jost font-medium text-2xl bg-white/60 px-8  flex items-center space-x-8;
}

.clients-swiper {
  @apply bg-greyishWhite xl:py-4;
}
.clients-swiper .swiper-slide img {
  @apply w-[70%] xl:w-[75%];
}

.services-info {
  @apply lg:flex items-start justify-between gap-10 space-y-8 lg:space-y-0 space-x-2 mb-16;
}

.service-items {
  @apply grid grid-cols-1 lg:grid-cols-3  md:grid-cols-2  gap-[3.75rem];
}
.service-items .service-item {
  @apply px-10 2xl:px-12 py-[2.25rem] bg-lightGray rounded-md cursor-pointer hover:scale-[101%] xl:hover:scale-105 transition-all duration-300 ease-in-out;
}

/*!
 * modified Simple lightbox effect in pure JS
 * @see {@link https://github.com/squeral/lightbox}
 * @see {@link https://github.com/squeral/lightbox/blob/master/lightbox.js}
 * passes jshint
 */
.iframe-lightbox,
.iframe-lightbox .backdrop {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.iframe-lightbox {
  display: none;
  position: fixed;
  opacity: 0;
  transition: opacity 0.2s ease;
  /*!
   * @see {@link https://github.com/englishextra/iframe-lightbox/issues/10}
   * @see {@link https://github.com/englishextra/iframe-lightbox/issues/14}
   */
  z-index: 999999;
}

.iframe-lightbox .backdrop {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.92);
  cursor: default;
}

.iframe-lightbox .content-holder {
  width: 80%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-right: -50%;
}

.iframe-lightbox .content {
  height: 0;
  position: relative;
  padding-bottom: 56.25%;
}

.iframe-lightbox .content > .body {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/*!
   * another pure css spinner
   * @see {@link https://epic-spinners.epicmax.co/}
   */
.iframe-lightbox .content > .body .half-circle-spinner,
.iframe-lightbox .content > .body .half-circle-spinner * {
  box-sizing: border-box;
}

.iframe-lightbox .content > .body .half-circle-spinner {
  width: 60px;
  height: 60px;
  border-radius: 100%;
  position: relative;
  margin: 0;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

.iframe-lightbox .content > .body .half-circle-spinner .circle {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 100%;
  border: calc(60px / 10) solid transparent;
}

.iframe-lightbox .content > .body .half-circle-spinner .circle.circle-1 {
  border-top-color: #ffffff;
  animation: half-circle-spinner-animation 1s infinite;
}

.iframe-lightbox .content > .body .half-circle-spinner .circle.circle-2 {
  border-bottom-color: #ffffff;
  animation: half-circle-spinner-animation 1s infinite alternate;
}

@keyframes half-circle-spinner-animation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.iframe-lightbox .content > .body.is-loaded .half-circle-spinner {
  display: none;
}

.iframe-lightbox iframe {
  display: block;
  width: 100%;
  height: 100%;
  border: 0;
  box-shadow: 0.267rem 0.267rem 0.267rem 0 rgba(3, 3, 3, 0.3);
  /*!
   * in js: iframe onload="this.style.opacity=1;" style="opacity:0;border:none;"
   */
  /* opacity: 0; */
  transition: opacity 0.2s ease;
}

.iframe-lightbox.is-showing {
  display: block;
}

.iframe-lightbox.is-opened {
  opacity: 1;
}

.iframe-lightbox .btn-close {
  display: block;
  position: fixed;
  top: 1rem;
  right: 1rem;
  width: 1.5rem;
  height: 1.5rem;
  font-size: 1rem;
  outline: none;
  cursor: pointer;
  border: 0;
  background-color: transparent;
}

/*!
   * pure css version
   * @see {@link https://codepen.io/brissmyr/pen/egidw}
   */
.iframe-lightbox .btn-close:before,
.iframe-lightbox .btn-close:after {
  content: " ";
  width: 0.125em;
  height: 1.5em;
  position: absolute;
  top: 0;
  right: 0.688em;
  background-color: #ffffff;
}

.iframe-lightbox .btn-close:before {
  transform: rotate(45deg);
}

.iframe-lightbox .btn-close:after {
  transform: rotate(-45deg);
}

/*!
   * @see {@link https://github.com/englishextra/iframe-lightbox/issues/12}
   */
.iframe-lightbox--open {
  overflow: hidden;
  -webkit-overflow-scrolling: touch;
  touch-action: auto;
  height: auto;
}